import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { createSelector } from 'reselect'
import Moment from 'react-moment';
import { useEffect } from 'react';
import { NavLink } from 'react-router-dom';

const calendarStrings = {
    lastDay: '[Yesterday at] LT',
    sameDay: '[Today at] LT',
    nextDay: '[Tomorrow at] LT',
    lastWeek: '[last] dddd [at] LT',
    nextWeek: 'dddd [at] LT',
    sameElse: 'MMMM D [at] LT'
};

const LIMIT = 5;

const ActivityLog = ({ taskId = null }) => {
    const filteredActivities = createSelector(
        state => (
            state.board.currBoard.activities
        ),
        activities => taskId ? activities.filter(activity => activity.taskId === taskId) : activities
    );
    const activities = useSelector(filteredActivities)

    const [showContent, setShowContent] = useState(true);
    const [showMore, setShowMore] = useState(true);
    const [list, setList] = useState(activities.slice(0, LIMIT));
    const [index, setIndex] = useState(LIMIT);

    useEffect(() => {
        setList(activities.slice(0, LIMIT))
        setShowMore(true)
        setIndex(LIMIT)
    }, [activities.length])

    const loadMore = () => {
        console.log('firstinDEX:', index)
        const newIndex = index + LIMIT;
        const newShowMore = newIndex <= (activities.length - 1);
        console.log('activities: ', activities, ' index: ', index, ' newIndex: ', newIndex);
        const newList = list.concat(activities.slice(index, newIndex));
        setIndex(newIndex);
        setList(newList);
        setShowMore(newShowMore);
    }
    const toggleContent = () => {
        setShowContent(prevState => (!prevState));
    }

    return (
        <div className="activity-log">
            {taskId && <button className="modal-btn" onClick={toggleContent}>{showContent ? 'Hide details' : 'Show details'}</button>}
            {showContent && list.length > 0 && list.map(activity => (
                <div className='activity-content' key={activity.id}>
                    {taskId ?
                        <p className="activity-desc" dangerouslySetInnerHTML={{ __html: activity.cardTxt }}></p> :
                        <p className="activity-desc" dangerouslySetInnerHTML={{ __html: activity.boardTxt }}></p>}
                    <Moment calendar={calendarStrings}>{activity.date}</Moment>
                </div>
            ))}
            {showContent && showMore && activities.length > LIMIT && <button className="clear-btn show-more-btn" onClick={loadMore}> Load More </button>}
        </div>
    )
}

export default ActivityLog
